.contacts {
    padding: 120px 0 110px 0;

    .container {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 100px;
    }
}

.contacts__map {
    min-width: 560px;
    width: 100%;
    min-height: 306px; 
}

.contacts__description {
    padding: 75px 0 0 0;

    p {
        margin: 0 0 30px 0;
        font-weight: 500;
        font-size: 18px;
        line-height: 27px;
    } 
}

.contacts__address {
    p {
        display: block;
        background-image: url("../img/svg/pin.svg");
        background-position: top left;
        background-repeat: no-repeat;
        padding: 0 0 0 28px;
        margin: 0 0 23px 0;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
    }

    a {
        display: block;
        padding: 0 0 0 28px;
        margin: 0 0 25px 0;
        background-image: url("../img/svg/phone.svg");
        background-position: top left;
        background-repeat: no-repeat;
    }
}

@include vp-1023 {
    .contacts {
        padding: 100px 0 95px 0;
    
        .container {
            gap: 30px;
        }
    }

    .contacts__map {
        min-width: 324px;
        min-height: 335px; 
    }
    
    .contacts__description {
        padding: 67px 0 0 0;
    
        p {
            margin: 0 0 26px 0;
        } 
    }
    
    .contacts__address {
        p {
            background-position: top 5px left;
            margin: 0 0 21px 0;
        }
    }
}

@include vp-767 {
    .contacts {
        padding: 60px 0 59px 0;
    
        .container {
            gap: 0;
            grid-template-columns: 1fr;
        }
    }

    .contacts__map {
        min-width: 180px;
        margin: 0 auto;
        min-height: 316px; 
    }
    
    .contacts__description {
        padding: 0;
        grid-row-start: 1;
    
        p {
            margin: 0 0 26px 0;
            max-width: 285px;
        } 
    }
    
    .contacts__address {
        p {
            background-position: top 7px left;
            margin: 0 0 22px 0;
        }

        a {
            background-position: top 3px left;
            margin: 0 0 35px 0;
        }
    }
}